import getInstanceLocalStorage from './localStorage';
import { MENU_ACTION_TYPES, STORAGE_DATA_TOOL, ACTIVE_CLASS } from './constants';

const {
 CHOOSE, PICKER, BUCKET, PENCIL,
} = MENU_ACTION_TYPES;
const LocalStorageInstance = getInstanceLocalStorage();

class MenuActionsClass {
    constructor() {
        this.activeTool = this.getActiveToolFromStorage();
        this.menuItems = document.querySelectorAll('.canvas-action');
    }

    removeActiveClass() {
        this.menuItems.forEach((el) => {
            el.classList.remove(ACTIVE_CLASS);
        });
    }

    setActiveClass(target) {
        target.classList.add(ACTIVE_CLASS);
    }

    setActiveTool(tool) {
        this.activeTool = tool;
    }

    changeActiveTool(target) {
        this.removeActiveClass();
        this.setActiveClass(target);
        this.setActiveTool(target.id);
    }

    setActiveTooltoStorage() {
        LocalStorageInstance.setData(STORAGE_DATA_TOOL, this.activeTool);
    }

    getActiveToolFromStorage() {
        const tool = LocalStorageInstance.getDataByType(STORAGE_DATA_TOOL);

        if (tool === BUCKET || tool === PICKER) {
            this.setActiveClass(document.getElementById(tool));
            return tool;
        }

        this.setActiveClass(document.getElementById(PENCIL));
        return PENCIL;
    }

    handleMenuAction(target) {
        const li = target.closest('.canvas-action');
        if (!li || li.id === CHOOSE) return;
        this.changeActiveTool(target);
    }

    serviceHotKeys(id) {
        const target = document.getElementById(id);
        if (id === CHOOSE) {
            this.inputElement.click();
        } else {
            this.changeActiveTool(target);
        }
    }

    handleHotKeys(e) {
        if (e.shiftKey) {
            switch (e.code) {
                case 'KeyP': this.serviceHotKeys(PENCIL); break;
                case 'KeyB': this.serviceHotKeys(BUCKET); break;
                case 'KeyC': this.serviceHotKeys(CHOOSE); break;
                case 'KeyD': this.serviceHotKeys(PICKER); break;
                default: break;
            }
        }
    }
}

export default MenuActionsClass;
