import getInstanceLocalStorage from './localStorage';

const LocalStorageInstance = getInstanceLocalStorage();

class ImageLoaderClass {
    constructor() {
        this.url = null;
    }

    imageRequest(city) {
        if (!city) return null;
        // My key:    57a89ccbf0154dca7b3f85d98dc3547d01024aa2b07b09f6243a2c56f2434617
        return fetch(`https://api.unsplash.com/photos/random?query=town,${city}
            &client_id=e2077ad31a806c894c460aec8f81bc2af4d09c4f8104ae3177bb809faf0eac17`)
            .then((res) => res.json())
            .then((data) => {
                this.url = data.urls.small;
                return data.urls.small;
            });
    }

    setUrlToStorage() {
        LocalStorageInstance.setData('url', this.url);
    }

    getUrlFromStorage() {
        const urlFromStorage = LocalStorageInstance.getDataByType('url');
        this.url = (!urlFromStorage) ? null : urlFromStorage;
    }

    handleImageLoader(city) {
        return this.imageRequest(city);
    }
}

export default ImageLoaderClass;
